/* @import url("https://fonts.googleapis.com/css?family=Poppins"); */
@import url("https://fonts.googleapis.com/css?family=Inter");
@import url("https://fonts.googleapis.com/css?family=Montserrat");
* {
  margin: 0;

  font-family: "Montserrat";
}

.card-container {
  /* box-shadow: rgba(0, 0, 0, 0.08) 0px 4px 12px; */
  box-shadow: 0px 0px 20px 0px #c4c4c440;

  display: flex;
  /* grid-template-columns: repeat(auto-fill, minmax(15rem, 1fr)); */
  width: 329px;
  height: 278px;
  border-radius: 10px;

  /* border: 1px solid; */
  /* box-shadow: 10px 10px 10px 0px rgba(70, 67, 67, 0.75); */
}
.card-img {
  height: 45px;
  width: 45px;
  margin-top: 9px;
  margin-left: 12px;
  border-radius: 100px;
}
.card-people {
  margin-top: 29px;
  margin-left: 10px;
}

.card-title {
  text-transform: capitalize;
  font-style: normal;
  font-weight: 600;
  font-size: 15px;
  line-height: 24px;

  color: #333333;

  margin-left: 10px;
  margin-top: -10px;
}
.card-occupation {
  margin-left: 10px;
  font-style: normal;
  font-weight: 400;
  font-size: 12px;
  text-align: justify;
  line-height: 17px;
  /* identical to box height */

  color: #333333;
}
.card-section {
  margin-left: 10px;
  margin-top: 15px;
  text-align: justify;
  overflow-wrap: break-word;

  text-align: center;
  font-style: normal;
  font-weight: 500;

  font-size: 13px;
  line-height: 17px;
  /* identical to box height */

  color: #396afc;

  width: 120px;
  padding: 5px;
  background: linear-gradient(
      0deg,
      rgba(57, 106, 252, 0.1),
      rgba(57, 106, 252, 0.1)
    ),
    #ffffff;
  border-radius: 5px;
}

.card-info {
  margin-top: 20px;

  font-style: normal;
  font-weight: 400;
  font-size: 12px;
  line-height: 17px;
  /* identical to box height */

  color: #02014f;
}
.icon {
  position: relative;
  font-size: 20px;
  margin: 3px;
  top: 10px;
}
